.featured {
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    z-index: -1;
    margin-bottom: -1rem;
}

.featuredItem {
    position: relative;
    color: white;
    border-radius: 10px;
    overflow: hidden;
    height: 250px;

}

.featuredImg {
    width: 100%;
    object-fit: cover;
    height: 95%;
    
}

.featuredTitles {
    position: absolute;
    bottom: 20px;
    left: 20px;
    /* margin-bottom: -4rem; */
}